.sorting__form {
	display: flex;
	flex-direction: column;
	gap: 15px;
}

.sorting__order {
	display: flex;
	flex-direction: column;
	margin: 0;
	padding: 0;
	border: none;
}

.sorting__favourites {
	display: flex;
	flex-direction: column;
}
.sorting__favourites h2 {
	align-self: flex-end;
}

.sorting__order-list {
	display: flex;
	flex-direction: row;
	margin: 5px 0 0 0;
	padding: 0;
	list-style: none;
}

.sorting__order-tab {
	display: flex;
	flex-grow: 1;
	font-size: 1rem;
}

.sorting__favourites-btn {
	display: flex;
	margin: 5px 0 0 0;
}
.sorting__favourites-svg {
	display: flex;
	margin: 5px 0 0 0;
}

.sorting__order-tab label,
.sorting__favourites-btn label {
	display: flex;
	justify-content: center;
	flex-grow: 1;
	padding: 7px 10px;
	color: var(--main-blue);
	transition: var(--animation);
	cursor: pointer;
	background-color: var(--white);
	border: 2px solid #e1e3ec;
}

.sorting__order-tab:first-of-type label {
	border-radius: 4px 0 0 4px;
}

.sorting__order-tab:not(:first-of-type) label {
	border-left: none;
}

.sorting__order-tab:last-of-type label {
	border-radius: 0 4px 4px 0;
}

.sorting__order-tab input:focus + label {
	outline: none;
	background-color: var(--main-blue);
	border-color: var(--main-blue);
	color: var(--white);
}

.sorting__order-tab input:checked + label {
	color: var(--white);
	background-color: var(--main-blue);
	border-color: var(--main-blue);
}

.sorting__order-tab label:active {
	outline: none;
	color: var(--white);
	background-color: var(--dark-blue);
	border-color: var(--dark-blue);
}

.sorting__order-tab input:disabled + label {
	color: var(--disabled-blue);
	background-color: var(--disabled-bg);
	cursor: default;
}

.sorting__order-tab input:disabled + label:active {
	border-color: var(--disabled-bg);
}

.sorting__order-tab input:checked:disabled + label,
.sorting__order-tab input:checked:disabled + label:active {
	background-color: var(--disabled-bg);
	border-color: var(--disabled-bg);
}

.sorting__favourites label {
	display: flex;
	align-items: center;
	padding: 7px 10px;	
	border: 2px solid var(--disabled-bg);
	border-radius: 4px;
	cursor: pointer;
	transition: var(--animation);
}

.sorting__favourites label svg {
	width: 19px;
	margin-right: 8px;
	fill: var(--main-blue);
	transition: inherit;
}

.sorting__favourites label:active {
	outline: none;
	color: #ffffff;
	border-color: var(--dark-blue);
	background-color: var(--dark-blue);
}

.sorting__favourites label:active svg {
	fill: #ffffff;
}

.sorting__favourites input:checked + label {
	color: var(--white);
	background-color: var(--main-blue);
	border-color: var(--main-blue);
}

.sorting__favourites input:checked + label svg {
	fill: var(--white);
}

.sorting__favourites input:disabled + label {
	color: var(--white);
	background-color: var(--disabled-blue);
	border-color: var(--disabled-blue);
	cursor: default;
}

.sorting__favourites input:disabled + label svg {
	fill: var(--white);
}
