.filter::-webkit-scrollbar {
	display: none;
}

.filter fieldset {
	margin-left: 0;
	margin-right: 0;
	margin-top: 0;
	padding: 0;
	border: none;
}

.filter select,
.xs__filters-popup select {
	display: flex;
	min-height: 38px;
	padding: 8px 0 5px 18px;
	border: 2px solid var(--disabled-bg);
	border-radius: 4px;
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	cursor: pointer;
	background: white url("../../img/arrow-for-select.svg") no-repeat 93% 52%;
}

.filter select:disabled + svg {
	fill: var(--disabled-blue);
	cursor: default;
}

.filter select:focus {
	outline: none;
	border: 2px solid var(--light-blue);
	padding: 8px 0 5px 18px;
}

.filter__select-wrapper {
	display: flex;
	flex-direction: column;
	gap: 8px;
}

.filter__select-block {
	display: flex;
	position: relative;
}

.filter__range {
	display: flex;
	flex-direction: column;
}

.filter__type legend {
	margin-bottom: 8px;
}

.filter__form,
.filter__estate,
.filter__camera,
.filter__laptop,
.filter__car {
	display: flex;
	flex-direction: column;
	gap: 15px;
}

.filter__type {
	margin-bottom: 8px;
}

.filter__button {
	margin-top: 10px;
	margin-bottom: 20px;
	display: flex;
	justify-content: center;
	align-items: center;
	min-width: 142px;
	min-height: 52px;
	border: none;
	padding: 0;
	background-color: var(--main-blue);
	border-radius: 4px;
	cursor: pointer;
	transition: 0.2s linear all;
}

.filter__button:focus, .filter__button:active {
	outline: none;
	background-color: var(--dark-blue);
}

.filter__button:disabled {
	background-color: var(--disabled-bg);
	cursor: default;
}

.filter__checkboxes-list {
	display: flex;
	flex-direction: column;
	gap: 10px;
	margin: 0;
	padding: 0;
	list-style: none;
	transition: var(--animation);
}
.filter__radiobuttons {
	display: flex;
	flex-direction: column;
	gap: 8px;
}
.filter__radiobuttons-list {
	display: flex;
	margin: 0;
	padding: 0;
	list-style: none;
	transition: var(--animation);
}

.filter__checkboxes-item {
	position: relative;
}

.filter__checkboxes-item input:focus + label {
	color: var(--light-blue);
}

.filter__checkboxes-list {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.filter__radiobuttons-list {
	display: flex;
	width: 100%;
}

.filter__radiobuttons legend {
	margin-bottom: 8px;
}

.filter__checkboxes-list label,
.filter__radiobuttons label {
	cursor: pointer;
	user-select: none;
}

.filter__checkboxes-list label {
	position: relative;
	display: flex;
	align-items: center;
	padding-left: 35px;
}

.filter__checkboxes-list label::before {
	content: "";
	position: absolute;
	left: 0px;
	display: block;
	width: 24px;
	height: 24px;
	background-color: #fff;
	border: 2px solid #e1e3ec;
	border-radius: 4px;
	transition: var(--animation);
}

.filter__checkboxes-list label::after {
	content: "";
	position: absolute;
	left: 0px;
	width: 24px;
	height: 24px;
	background-image: url("../../img/icon-tick.svg");
	background-repeat: no-repeat;
	background-position: center;
	display: none;
	transition: var(--animation);
}

.filter__checkboxes-list input:checked + label::before {
	background-color: var(--dark-blue);
	width: 24px;
	height: 24px;
	border-color: var(--dark-blue);
}

.filter__checkboxes-list input:checked + label::after {
	display: block;
}

.filter__min-square input {
	display: block;
	box-sizing: border-box;
	width: 100%;
	min-height: 42px;
	padding-left: 15px;
	border: 2px solid #e1e3ec;
	border-radius: 4px;
	background-color: var(--white);
	appearance: none;
	transition: var(--animation);
	-moz-appearance: textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
	display: none;
	-webkit-appearance: none;
	margin: 0;
}

.filter__min-square input:focus::placeholder {
	opacity: 0;
}

.filter__min-square input:active {
	outline: none;
	border-color: var(--dark-blue);
	cursor: text;
}

.filter__min-square input:focus {
	outline: none;
	border-color: var(--dark-blue);
}

.filter__min-square input:disabled {
	background-color: var(--disabled-bg);
	color: var(--disabed-blue);
}

.filter__min-square input:disabled:active {
	cursor: default;
}

.filter__radiobuttons label {
	border: 2px solid #e1e3ec;
	transition: var(--animation);
}

.filter__radiobuttons-item:not(:first-of-type) label {
	border-left: none;
}

.filter__radiobuttons-item:first-of-type label {
	border-radius: 4px 0 0 4px;
}

.filter__radiobuttons-item:last-of-type label {
	border-radius: 0 4px 4px 0;
}

.filter__radiobuttons-item label {
	display: flex;
	align-items: center;
	justify-content: center;
	margin: 0;
	padding-top: 5px;
	padding-bottom: 5px;
	background-color: #fff;
}

.filter__radiobuttons--rooms {
	margin-bottom: 35px;
}

.filter__radiobuttons input:checked + label {
	color: #ffffff;
	background-color: var(--main-blue);
	border-color: var(--main-blue);
}

.filter__radiobuttons input:active:disabled + label {
	background-color: var(--main-bg);
}

.filter__radiobuttons input:disabled + label {
	background-color: var(--main-bg);
	color: var(--disabled-blue);
	cursor: default;
}

.filter__checkboxes-item label:active::before {
	background-color: var(--dark-blue);
	border-color: var(--dark-blue);
}

.filter__checkboxes-item input:disabled + label {
	color: var(--disabled-blue);
	cursor: default;
}

.filter__checkboxes-item input:disabled + label::before {
	background-color: var(--disabled-bg);
	cursor: default;
}

.filter__checkboxes-item input:checked:disabled + label::before {
	background-image: url("../../img/icon-tick_disabled.svg");
	background-color: var(--disabled-bg);
	border-color: var(--disabled-bg);
	cursor: default;
}

.filter__checkboxes-item input:checked:disabled + label {
	color: var(--disabled-blue);
	cursor: default;
}

.filter__checkboxes-item input:focus:checked + label::before {
	border-color: var(--dark-blue);
}

.filter__checkboxes-item input:focus + label::before {
	border-color: var(--light-blue);
}

.filter__checkboxes-item label:active {
	color: var(--dark-blue);
}

.filter__radiobuttons label:active {
	background-color: var(--dark-blue);
	border-color: var(--dark-blue);
}

.filter__radiobuttons input:disabled:checked + label {
	background-color: var(--disabled-bg);
	border-color: var(--disabled-bg);
}

.filter__radiobuttons input:focus:not(:checked) + label {
	background-color: var(--light-blue);
	border-color: var(--light-blue);
	color: #ffffff;
}

.filter__type--car-body {
	margin-bottom: 22px;
}

.filter__ram-list {
	display: flex;
	margin: 0;
	padding: 0;
	list-style: none;
}

.filter__checkboxes-item input:focus:checked + label {
	color: var(--main-blue);
}

.filter__checkboxes-item input:focus:not(:checked) + label::before {
	border-color: #e1e3ec;
}

.filter__checkboxes-item input:not(:checked):focus + label:focus {
	color: var(--light-blue);
}

.filter__radiobuttons-item {
	flex-grow: 1;
}
